.meme-item {
	text-align: left;
	display: inline-block;
	padding: 10px;
	position: relative;
	width: 150px;
	height: 150px;
	margin: 5px;
	cursor: pointer;
}

.meme-img {
	width: 150px;
	height: 150px;
	border-radius: 8px;
	border: 3px solid #fff;
	position: absolute;
	object-fit: cover;
	filter: brightness(90%);
}

.my-meme-img {
	position: relative;
	width: 150px;
	border-radius: 8px;
	display: inline-block;
	margin: 6px;
	border: 3px solid #fff;
	vertical-align: middle;
}
.my-meme-img-container {
	display: inline-block;
	position: relative;
}
.my-meme-close {
	position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    background-color: #ff6b6b;
    border: 10px;
    width: 20px;
    height: 20px;
    line-height: 19px;
    font-size: 18px;
	border-radius: 20px;
	cursor: pointer;
	filter: brightness(80%);
}
.my-meme-save {
	position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    background-color: #0abde3;
    border: 10px;
    width: 20px;
    height: 20px;
    line-height: 19px;
    font-size: 18px;
	border-radius: 20px;
	cursor: pointer;
	filter: brightness(80%);
}
.my-meme-close:hover, .my-meme-save:hover {
	transform: scale(1.3);
	transition: 0.2s ease-in-out;
	filter: brightness(100%);
}
.meme-txt {
	width: 150px;
	text-align: center;
	position: absolute;
	border-radius: 5px;
	bottom: 0;
	animation-name: fadeIn;
	animation-duration: 1s;
	font-size: 14px;
}

.no-txt {
	display: none;
}

.darken-img {
	background-color: grey;
	filter: brightness(10%);
	animation-name: darken;
	animation-duration: 1s;
}

@keyframes darken {
	from {
		filter: brightness(90%);
	}
	to {
		filter: brightness(10%);
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 100;
	}
}

